@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@400;500;600&display=swap');

/* custom css presets */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Lexend', sans-serif;
}
button {
  font-size: 18px;
  padding: 0.3em 1em;
  border: none;
}
a {
  color: inherit;
  text-decoration: none;
}
a:hover {
  color: inherit;
  text-decoration: none;
}
/* Multi use css */
.page {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
/* class styles */
.App {
  position: relative;
}
.landing-page {
  text-align: center;
  background-image: linear-gradient(
    to right top,
    #f4f4f4,
    #f2f7fb,
    #ecfbfd,
    #ecfff6,
    #f9ffeb
  );
}
.landing-page h1 {
  font-size: 3em;
  margin-bottom: 1em;
  color: #919191;
  text-shadow: 5px 5px 5px #e7e7e7;
  /* background: -webkit-linear-gradient(#c7c7c7, #333);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; */
}
.landing-page img {
  width: 80%;
  max-width: 600px;
}
.landing-page button {
  display: block;
  margin: 2em auto 0;
  border-radius: 50px;
  background: #f1f8fc;
  color: #575757;
  box-shadow: 6px 6px 12px #cdd3d6, -6px -6px 12px #ffffff;
}
.landing-page .developer {
  color: #888888;
  position: absolute;
  bottom: 1%;
  left: 50%;
  transform: translateX(-50%);
}
/* player page */
.player-page {
  text-align: center;
  background-image: linear-gradient(
    to right top,
    #f4f4f4,
    #f2f7fb,
    #ecfbfd,
    #ecfff6,
    #f9ffeb
  );
}
.player-page img {
  width: 100px;
  margin-bottom: 2em;
}
.player-page input {
  border: 1px solid #cfcfcf;
  border-radius: 3px;
  padding: 0.5em 1em;
  width: 300px;
  display: inline;
}
.player-page .input-icon {
  width: 18px;
}
.player-page button {
  display: block;
  margin: 1em auto 0;
  background-image: linear-gradient(to top, #4481eb 0%, #04befe 100%);
  color: #fff;
  border-radius: 25px;
}
/* game page */
.game-page {
  height: 100vh;
  width: 100%;
  position: relative;
  overflow: hidden;
}
.game-page .statistics {
  position: relative;
  width: 100%;
  background-color: #fd5e45;
  padding: 1em;
  text-align: center;
  margin-bottom: 1em;
  color: #fff;
  box-shadow: 5px 5px 10px rgb(194, 194, 194);
}
.game-page .statistics h3 {
  font-size: 1.5em;
  background-color: #fff;
  display: inline-block;
  color: #555555;
  padding: 0.3em 1em;
}
.game-page .statistics .player {
  height: 70px;
  width: 70px;
  border-radius: 50%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-size: 1.8em;
  font-weight: bold;
  box-shadow: 0px 5px 10px rgb(117, 117, 117);
  border-radius: 42px;
  background: linear-gradient(145deg, #ffffff, #cacaca);
  color: #fd5e45;
}
.game-page .statistics .player.p1 {
  bottom: -25px;
  left: 30px;
}
.game-page .statistics .player.p2 {
  bottom: -25px;
  right: 30px;
}
.game-page .player-info {
  margin-top: 30px;
  padding: 0.5em 0;
  text-align: center;
}
.game-page .player-info img {
  width: 40px;
}
.game-page .play-board {
  margin: 1em auto 2em;
  text-align: center;
  width: 300px;
  background: linear-gradient(145deg, #d9dfe3, #ffffff);
  box-shadow: 8px 8px 17px #cdd3d6, -8px -8px 17px #ffffff;
}
.game-page .play-board .row {
  margin: 0;
  padding: 0;
}
.game-page .play-board .cols {
  padding: 0;
}
.box {
  padding: 2em 0;
  border: 1px solid #a7a7a7;
}
.box img {
  width: 25px;
}
.box.b0,
.box.b1,
.box.b2 {
  border-top: 0;
}
.box.b2,
.box.b5,
.box.b8 {
  border-right: 0;
}
.box.b6,
.box.b7,
.box.b8 {
  border-bottom: 0;
}
.box.b0,
.box.b3,
.box.b6 {
  border-left: 0;
}
.game-page .hint-message {
  color: #a5a5a5;
}
.game-page .restart-btn {
  display: block;
  box-shadow: 5px 5px 1px #b9b9b9;
  margin-top: 1em;
}
.game-page .quit-btn {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  background-color: crimson;
  color: #fff;
  position: absolute;
  bottom: 20px;
  right: 20px;
  box-shadow: 3px 3px 8px rgb(185, 185, 185);
}

.my-modal {
  height: 100vh;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: #ff7e06;
  color: #fff;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  overflow: hidden;
}
.my-modal img {
  width: 80%;
  margin-bottom: 1em;
}
.my-modal button {
  display: block;
  margin: auto;
  width: 180px;
  margin-top: 0.8em;
  box-shadow: 5px 5px 1px rgb(131, 131, 131);
}
.my-modal .developer {
  width: 100%;
  color: #fff;
  position: absolute;
  bottom: 1%;
  left: 50%;
  transform: translateX(-50%);
}
@media (max-width: 680px) {
  .Toastify__toast {
    width: 300px !important;
    margin: auto;
  }
}
@media (min-width: 680px) {
  * {
    font-size: 20px;
  }
  .landing-page button {
    box-shadow: 10px 10px 20px #cdd3d6, -10px -10px 20px #ffffff;
    font-size: 20px;
  }
  .game-page .statistics .player.p1 {
    left: 200px;
  }
  .game-page .statistics .player.p2 {
    right: 200px;
  }
  .game-page .play-board {
    width: 400px;
  }
  .my-modal img {
    width: 400px;
  }
}
